.container{
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: linear-gradient(#121249,#121265);
}
.title{
    font-size: 85px;
    color: white;
}
.inputext{
    background-color: rgba(255, 255, 255, 0.2);
    padding: 15px;
    margin: 34px;
    display: flex;
    border-radius: 8px;
    box-shadow: 1px 3px 8px rgba(0, 0, 0, 0.6);

}
.inputext input{
    background-color: transparent;
    border: 0;
    font-size: 25px;
    color: white;
    outline: none;
    margin-right: 8px;
}
.inputext input::placeholder{
    color: #f1f1f1;
}
.botao{
    background-color: transparent;
    border: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    transition: transform 0.3s;
}
.botao:hover{
    transform: scale(1.2);
}
.main{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background-color: white;
    width: 500px;
    border-radius: 8px;
}
.main h2{
    margin: 16px;
    font-size: 40px;
}
.main span{
    margin-bottom: 16px;
    font-weight: bold;
}

@media (max-width: 620px){
    .title{
        font-size: 60px;
    }
    .main h2{
        font-size: 28px;
    }
    .main{
        width: 80%;
        min-height: 250px;
    }
}